body .auth-header {
  padding: 35px 0;
}
body .auth-header .auth-title {
  text-align: center;
  font-size: 34px;
  color: #3cc51f;
  font-weight: 400;
  margin: 0 15%;
}
body .auth-header .auth-sub-title {
  text-align: center;
  color: #888;
  font-size: 14px;
}
body .auth-header .auth-second-title {
  text-align: center;
  font-size: 24px;
  color: #3cc51f;
  font-weight: 400;
  margin: 0 15%;
}
body .help-block {
  color: red;
}
body .login .login-error {
  justify-content: center;
  align-items: center;
}
body .login .login-submit {
  width: 80%;
  margin: 5px auto;
}
body .login .login-forget {
  position: relative;
  margin-right: 0px;
}
body .login .login-row {
  padding: 10px 10%;
  position: relative;
  display: flex;
  align-items: center;
}
body .register .login-error {
  justify-content: center;
  align-items: center;
}
body .register .login-submit {
  width: 80%;
  margin: 5px auto;
}
body .register .login-row {
  padding: 10px 10%;
  position: relative;
  display: flex;
  align-items: center;
}
body div.swiper-container {
  width: 100%;
  height: 200px;
}
body div.swiper-container .swiper-slide img {
  width: 100%;
  height: 200px;
}
body div.category {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: left;
  color: white;
  margin-bottom: 10px;
}
body div.category .category_item {
  font-size: 15px;
}
body div.category .category_item a {
  white-space: nowrap;
  margin-left: 5px;
}
body div.home_container {
  width: 100%;
  display: flex;
  flex-direction: row;
}
body div.home_container div.left {
  width: 20%;
  display: flex;
  flex-direction: column;
  border-right: 1px solid #eee;
}
body div.home_container div.left div.category_item {
  text-align: center;
  border-bottom: 1px solid #eee;
  background: white;
}
body div.home_container div.left div.category_item a {
  font-size: 10px;
  color: black;
}
body div.home_container div.left div.category_item_active {
  background: #aaa;
}
body div.home_container div.left div.category_item_active a {
  font-size: 12px;
  color: white;
}
body div.home_container div.right {
  width: 80%;
}
body div.home_container div.right .good_item {
  margin: 0;
  border-top: 1px solid #eee;
  width: 100%;
}
body div.home_container div.right .good_item .cart {
  position: absolute;
  flex: 1;
  min-width: 0;
  right: 10px;
  bottom: 10px;
}
body div.home_container div.right .good_item .cart a {
  color: #ccc;
}
body div.home_container div.right .good_item .cart span {
  color: green;
}
body div.home_container div.right .weui_media_box {
  padding: 5px;
}
body div.weui_tabbar {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
}
body div.weui_tabbar .weui_tabbar_item {
  padding-top: 5px;
}
body div.weui_tabbar .weui_tabbar_label {
  margin-top: 2px;
}
