body {
    .auth-header {
        padding: 35px 0;
        .auth-title {
            text-align: center;
            font-size: 34px;
            color: #3cc51f;
            font-weight: 400;
            margin: 0 15%;
        }
        .auth-sub-title {
            text-align: center;
            color: #888;
            font-size: 14px;
        }
        .auth-second-title {
            text-align: center;
            font-size: 24px;
            color: #3cc51f;
            font-weight: 400;
            margin: 0 15%;
        }
    }
    .help-block {
        color: red;
    }
    .login {
        .login-error {
            justify-content: center;
            align-items: center;
        }
        .login-submit {
            width: 80%;
            margin: 5px auto;
        }
        .login-forget {
            position: relative;
            margin-right: 0px;
        }
        .login-row {
            padding: 10px 10%;
            position: relative;
            display: flex;
            align-items: center;
        }
    }
    .register {
        .login-error {
            justify-content: center;
            align-items: center;
        }
        .login-submit {
            width: 80%;
            margin: 5px auto;
        }
        .login-row {
            padding: 10px 10%;
            position: relative;
            display: flex;
            align-items: center;
        }
    }
    div.swiper-container {
        width: 100%;
        height: 200px;
        .swiper-slide {
            img {
                width: 100%;
                height: 200px;
            }
        }
    }
    div.category {
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: left;
        color: white;
        margin-bottom: 10px;
        .category_item {
            font-size: 15px;
            a {
                white-space: nowrap;
                margin-left: 5px;
            }
        }
    }
    div.home_container {
        width: 100%;
        display: flex;
        flex-direction: row;
        div.left {
            width: 20%;
            display: flex;
            flex-direction: column;
            border-right: 1px solid #eee;
            div.category_item {
                text-align: center;
                border-bottom: 1px solid #eee;
                a {
                    font-size: 10px;
                    color: black;
                }
                background: white;
            }
            div.category_item_active {
                a {
                    font-size: 12px;
                    color: white;
                }
                background: #aaa;
            }
        }
        div.right {
            width: 80%;
            .good_item {
                margin: 0;
                border-top: 1px solid #eee;
                width: 100%;
                .cart {
                    position: absolute;
                    flex: 1;
                    min-width: 0;
                    right: 10px;
                    bottom: 10px;
                    a {
                        color: #ccc;
                    }
                    span {
                        color: green;
                    }
                }
            }
            .weui_media_box {
                padding: 5px;
            }
        }
    }
    div.weui_tabbar {
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        .weui_tabbar_item {
            padding-top: 5px;
        }
        .weui_tabbar_label {
            margin-top: 2px;
        }
    }
}
